  <template>
    <div style="user-container">

      <div class="userinfo-container">
        <div class="userinfo">
          <img class="userinfo-avatar" v-if="userInfo.avatarUrl" :src="userInfo.avatarUrl" background-size="cover" />
          <div class="userinfo-nickname">
            <card :text="userInfo.nickName"></card>
          </div>
        </div>
      </div>

      <!-- <div style="background: #efefef; height:15rpx; width:100%;"> 
      </div>

      <div class="flex-container">
        <div class="flex-item">
          <img class="flex-img" :src="userUrl" alt="lock">
          <div class="placeholder">个人资料</div>
        </div>
        <div class="flex-item">
          <img class="flex-img" :src="lockUrl" alt="lock">
          <div class="placeholder">修改密码</div>
        </div>
        <div class="flex-item">
          <img class="flex-img" :src="calendarUrl" alt="lock">
          <div class="placeholder">我的活动</div>
        </div>
        <navigator class="flex-item" url="../integral/integral">
          <img class="flex-img" :src="integUrl" alt="lock">
          <div class="placeholder">我的积分</div>
        </navigator>
      </div> -->


      <div style="background: #efefef; height:15rpx; width:100%;"> 
      </div>


      <div class="list-container"> 
        <navigator class="list-item" url="../integral/integral">
          <img class="img-avatar" :src="integUrl" alt="user-avatar">
          <span>我的积分</span>
          <div class="arrow_class"></div>
        </navigator>
        <navigator class="list-item" url="../integral/integral">
          <img class="img-avatar" :src="calendarUrl" alt="user-avatar">
          <span>我的活动</span>
          <div class="arrow_class"></div>
        </navigator>
        <view class="list-item" @click="chooseAddress">
          <img class="img-avatar" :src="boxUrl" alt="user-avatar">
          <span>收货地址</span>
          <div class="arrow_class"></div>
        </view>
        <navigator class="list-item xian" url="" >
          <img class="img-avatar" :src="atmUrl" alt="user-avatar">
          <span>退出登录</span>
          <div class="arrow_class"></div>
        </navigator>
      </div>

      


    </div>
  </template>

  <script>
  import card from '@/components/card'

  export default {
    data () {
      return {
        motto: 'Hello World',
        userInfo: {},
        userUrl: '../../images/account.png',
        lockUrl: '../../images/password.png',
        atmUrl: '../../images/atm.png',
        integUrl: '../../images/integral.png',
        boxUrl: '../../images/box.png',
        calendarUrl: '../../images/calendar.png'
      }
    },

    components: {
      card
    },

    methods: {
      bindViewTap () {
        const url = '../rename/rename'
        wx.navigateTo({ url })
      },
      getUserInfo () {
        // 调用登录接口
        wx.login({
          success: () => {
            wx.getUserInfo({
              success: (res) => {
                this.userInfo = res.userInfo
              }
            })
          }
        })
      },
      clickHandle (msg, ev) {
        console.log('clickHandle:', msg, ev)
      },
      // 选择收货地址
      chooseAddress: function () {
        const _self = this
        wx.chooseAddress({
          success: function (res) {
          // 地址信息
          // console.log(res.userName)      用户名
          // console.log(res.postalCode)    邮编
          // console.log(res.provinceName)  省
          // console.log(res.cityName)      市
          // console.log(res.countyName)    区
          // console.log(res.detailInfo)    详细地址
          // console.log(res.nationalCode)  国家码
          // console.log(res.telNumber)     手机号码
            _self.setData({
              addressInfo: res,
              addressHidden: true
            })
          },
          fail: (res) => {
            wx.onNetworkStatusChange(function (res) {
              if (res.isConnected) {
                wx.showToast({
                  title: '当前网络不可用，请检查网络设置'
                })
              }
            })
          }
        })
      }
    },

    created () {
      // 调用应用实例的方法获取全局数据
      this.getUserInfo()
    }
  }
  </script>

  <style scoped>
  .user-container {
    width: 100%;
    height: 100%;
  }

  .userinfo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .userinfo-container {
    width: 100%;
    height: 400rpx;
    margin-bottom: 15rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    background: white;
  }

  .userinfo-avatar {
    width: 126rpx;
    height: 126rpx;
    padding: 2rpx;
    border-radius: 50%;
    border: 1px solid gray;
  }

  .list-container {
    position: relative;
    width: 100%;
    font-size: 15px;
    margin-top: 15rpx 0;
    background: white;
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }
  
  .list-item{
    height: 100rpx;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .list-item:not(:first-child)::after{
    content: "";
    position: absolute;
    top: 0;
    left: 30rpx;
    width: 100%;
    height: 1px;
    background: #e6e6e6;
  }

  .list-item img{
    width: 20px;
    height: 20px;
    margin: 0 15px 0 15px;
  }

  .list-item span{
    position: absolute;
    display: inline-block;
    line-height: 15px;
    left: 36px;
    margin: 0 10rpx;
  }

  .img-avatar {
    position: absolute;
    left: 0px;
  }

  .img-lock {
    position: absolute;
    right: 0px;
  }

  .arrow_class {
    right: 20px;
    position: absolute;
  }

  .arrow_class:after{
    content: " ";
    display: inline-block;
    height: 10px;
    width: 10px;
    border-width: 2px 2px 0 0;
    border-color: #c8c8cd;
    border-style: solid;
    -webkit-transform: matrix(0.71, 0.71, -.71, 0.71, 0, 0);
    transform: matrix(0.71, 0.71, -.71, 0.71, 0, 0);
  }

  .flex-container {
    margin: 15rpx 0;
    background: white;
    width: 750rpx;
    height: 168rpx;
    padding: 30rpx 30rpx;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .flex-item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 25%;
    height: 150rpx;

  }

  .flex-img {
    width: 50rpx;
    height: 50rpx;
    display: block;
  }

  .placeholder {
    font-size: 16px;
  }

  </style>



